<template>
    <div class="pasenges">
      <div class="top-bg"></div>
      <div class="div-placeholder"></div>
      <div class="frame header-section">
          <div class="identity">
              <img @click="headClickHandle" class="identity-left" :src="icon"/>
              <div class="identity-right">
                <!-- 未登录 -->
                <template v-if="!token">
                  <div class="right-top">
                    <span @click="headClickHandle" class="right-item">立即登录</span>
                    <span class="job">{{roleName}}</span>
                  </div>
                </template>
                <!-- 已经登录 -->
                <template v-else>
                  <!-- 普通用户&美体师 -->
                  <template v-if="role=='1'||role=='6'">
                    <div class="right-top">
                      <span class="right-item offset">{{username}}</span>
                      <span class="job">{{roleName}}</span>
                    </div>
                    <span class="right-bottom" v-if="!bindStoreInfo" @click="bindStoreHandle">未绑定店铺</span>
                    <span class="right-bottom" v-else>已绑定：{{bindStoreInfo.storename}}</span>
                  </template>
                  <!-- 商家 -->
                  <template v-else-if="role=='2'">
                    <div class="right-top">
                      <span class="right-item offset">{{storeInfo.storename?storeInfo.storename:'未改名'}}</span>
                      <span class="job">{{roleName}}</span>
                    </div>
                    <span class="right-bottom">编号：{{storeInfo.storekey}}</span>
                  </template>
                  <!-- 非用户非商家 -->
                  <template v-else>
                    <div class="right-top">
                      <span class="right-item">{{username}}</span>
                      <span class="job">{{roleName}}</span>
                    </div>
                  </template>
                </template>
              </div>
          </div>
          <div class="data">
            <div class="data-item">
                <p class="num">{{accounts}}</p>
                <p class="data-name">余额</p>
            </div>
            <div class="data-item">
                <p class="num">{{source}}</p>
                <p class="data-name">积分</p>
            </div>
            <div class="data-item">
                <p class="num">{{expnum}}</p>
                <p class="data-name">体验券</p>
            </div>
            <div class="data-item">
                <p class="num">{{giftnum}}</p>
                <p class="data-name">礼品券</p>
            </div>
          </div>
      </div>
      <div class="frame service-section">
        <p class="frame-title">我的服务</p>
        <div class="function-cell-list">
          <div v-for="item in serviceArr" class="function-cell" @click="cellClickHandle(item,$event)">
            <span :class="'iconfont '+item.icon"></span>
            <p class="icon-name">{{item.names}}</p>
          </div>
        </div>
      </div>
      <div v-show="this.helpArr&&this.helpArr.length!=0" class="frame help-section">
        <p class="frame-title">帮助中心</p>
        <div class="function-cell-list">
          <div v-for="item in helpArr" class="function-cell" @click="cellClickHandle(item,$event)">
            <span :class="'iconfont '+item.icon"></span>
            <p class="icon-name">{{item.names}}</p>
          </div>
        </div>
      </div>
      <mt-button v-show="token" size="large" class="login-out-btn" type="default" @click.native="loginOutHandle">退出登录</mt-button>
      <div class="change-name-popup" v-show="showChangeNamePop" @touchmove.prevent>
        <div class="pop-content">
          <p class="change-name-tip">*请填写您的店铺名称*</p>
          <mt-button size="small" class="change-btn" type="default" @click="jumpToChangeName">去填写</mt-button>
        </div>
        <div class="pop-bg"></div>
      </div>
      <!-- 扫码美体师选择框 -->
      <choose-therapist v-if="role=='2'" ref="therapist" @storeCallScan="storeScanHandle"></choose-therapist>
    </div>
</template>
<script>
import meConfig from "./meConfig";
import { mapState } from "vuex";
import Vue from "vue";
import chooseTherapist from "../megoods/store/chooseTherapist.vue";
import wxqrcode from "../../common/js/wxqrcode";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);
import { savaToLocal, loadFromlLocal } from "../../common/js/storage.js"; // 公共方法：本地缓存

export default {
  name: "v-me",
  data() {
    return {
      memoryScroll:0,
      roleName: "",
      serviceArr: [],
      helpArr: null,
      resultObj: {},
      intervalId: -1000,
      deductTherapist: null //提成的美体师
    };
  },
  components: {
    chooseTherapist
  },
  mounted() {
    //this.wxReady();
    this.initFunction();
  },
  beforeRouteLeave(to, from, next) {
    // this.$store.commit("UPDATE_NOBINDSHOW", false);
    if (this.intervalId != -1000) {
      window.clearInterval(this.intervalId);
      this.intervalId = -1000;
    }
    this.memoryScroll = document.body.scrollTop;
    next();
  },
  watch: {
    token: "initFunction",
    role: "initFunction"
  },
  activated() {
    this.refreshInfoInTime();
    // this.initFunction();
    document.body.scrollTop = this.memoryScroll;
    if (this.intervalId == -1000)
      this.intervalId = setInterval(this.intervalHandle, 3000);
  },
  methods: {
    jumpToChangeName() {
      this.$router.push({ name: "storeBindName" });
    },
    intervalHandle() {
      if (!this.token) return;
      this.refreshInfoInTime();
    },
    initFunction() {
      console.log("this.role=" + this.role);
      let configItem = meConfig[this.role.toString()];
      if (this.token) this.roleName = configItem.roleName;
      else this.roleName = "访客";
      this.serviceArr = configItem.service;
      this.helpArr = configItem.help;
    },
    QrCodeEnd(results) {
      //results
      // results = "fuc:bind,userid:20";
      // results = "fuc:integral,userid:136,id:5aa4e3e2ab7c1";
      var resultsArr = results.split(",");
      if (resultsArr.length < 2) {
        return;
      }
      resultsArr.forEach((val, index, arr) => {
        let tempArr = val.split(":");
        this.resultObj[tempArr[0]] = tempArr[1];
      });

      // alert('results='+results);

      if (!this.resultObj.fuc) return;
      if (this.resultObj.fuc == "good") {
        //礼品券扫码
        this.requestUseGood();
      } else if (this.resultObj.fuc == "bind") {
        //绑定关系扫码
        this.requestCanBind();
        console.log("this.resultObj.fuc=bind");
      } else if (this.resultObj.fuc == "integral") {
        this.requestUseIntegral();
      }
    },
    requestCanBind() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/suncode/dealfuc.php`, {
          fuc: "deposit_canpay",
          clientid: this.userid,
          salesmanid: this.resultObj.userid,
          dptype: 1
        })
        .then(response => {
          let data = response.body;
          if (data.error == "ok") this.jumpToPledgePage();
          else this.showMsg(data.msg);
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    jumpToPledgePage() {
      let pledgeInfo = {
        salesmanid: this.resultObj.userid
      };
      savaToLocal("aml", "pledgeInfo", pledgeInfo);
      this.$router.push({ path: "/pledgeAffirm" });
    },
    requestUseGood() {
      this.$store.dispatch("toggleLoading", true);
      // alert('resultObj='+this.resultObj.id+'=ddd='+this.resultObj.userid+'bid='+this.userid);

      this.$http
        .post(this.path + `php/suncode/dealfuc.php`, {
          fuc: "usecoupon",
          cuid: this.resultObj.id,
          userid: this.resultObj.userid,
          bossuserid: this.userid,
          buyid: this.resultObj.buyId,
          beautiesid: this.deductTherapist ? this.deductTherapist.beautiesid : 0
        })
        .then(response => {
          let data = response.body;
          if (data.error == "ok") this.scanGoodSuccessHandle();
          else this.showMsg(data.msg);
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    requestUseIntegral() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/Route.php`, {
          type: "usesourcegoods",
          uid: this.resultObj.userid,
          bid: this.userid,
          oid: this.resultObj.id
        })
        .then(response => {
          let data = response.body;
          if (data.error == "ok") this.scanGoodSuccessHandle();
          else this.showMsg(data.msg);
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    scanGoodSuccessHandle() {
      this.showMsg("操作成功");
      this.updatateUserInfo(this.initFunction);
    },
    showMsg(msg) {
      Toast({
        message: msg,
        iconClass: "iconfont icon-msnui-success",
        duration: 2500
      });
    },
    cellClickHandle(cellData, event) {
      ////this.onScanQr(event);
      //this.QrCodeEnd("fuc:integral,userid:21,id:5aa3e888ab21c");
      //this.QrCodeEnd("");//
      if (cellData.names == "扫一扫") this.onScanQr(event);
      else if (cellData.names == "联系我们")
        window.location.href = "tel:4001368868";
      else this.$router.push({ name: cellData.tourl });
    },
    onScanQr(event) {
      if (!this.token) {
        this.loginHandle();
        return;
      }
      if (this.role.toString() == "2") {
        this.$refs.therapist.resetHandle();
        this.$refs.therapist.show();
      } else wxqrcode.wxscanQRCode(this);
    },
    storeScanHandle(deductTherapist) {
      this.deductTherapist = deductTherapist;
      this.$refs.therapist.hide();
      wxqrcode.wxscanQRCode(this);
    },
    loginOutHandle() {
      if (this.token) {
        this.resetUserData();
      }
      this.loginHandle();
    },
    loginHandle() {
      this.$router.push({ path: "/login" });
    },
    headClickHandle() {
      if (this.token) return;
      this.loginHandle();
    },
    bindStoreHandle() {
      this.$router.push({ path: "/bindStore" });
    }
  },
  computed: {
    ...mapState([
      "openid",
      "path",
      "userid",
      "accounts",
      "role",
      "clientNum",
      "giftnum",
      "expnum",
      "username",
      "icon",
      "phoneno",
      "source",
      "token",
      "storeInfo",
      "bindStoreInfo"
    ]),
    showChangeNamePop() {
      if (this.role.toString() == "2" && !this.storeInfo.storename) return true;
      else return false;
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.pasenges {
  width: 100%;
  box-sizing: border-box;
  min-height: 100%;
  padding-bottom: 0.48rem;
  position: relative;

  .change-name-popup {
    position: relative;

    .pop-content {
      position: fixed;
      transform: translate(-50%, -50%);
      left: 50%;
      top: 50%;
      z-index: 19;
      width: 80%;
      border-radius: 8px;
      background-color: #fff;
      text-align: center;
      padding-top: 0.34rem;
      padding-bottom: 0.25rem;

      .change-name-tip {
        color: #595656;
        font-size: 0.16rem;
        letter-spacing: 0.01rem;
      }

      .change-btn {
        margin-top: 0.25rem;
        width: 60%;
        height: 0.3rem;
        background-color: #f8ef94;
        color: #595656;
        letter-spacing: 0.02rem;
        font-size: 0.15rem;
      }
    }

    .pop-bg {
      position: fixed;
      z-index: 18;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: #000;
      opacity: 0.4;
    }
  }

  .top-bg {
    position: absolute;
    top: 0;
    width: 100%;
    height: 1.1rem;
    background-color: #f8ef94;
  }

  .div-placeholder {
    height: 0.32rem;
  }

  .frame {
    margin: 0 0.1rem;
    background-color: #fff;
    border-radius: 0.06rem;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;

    .frame-title {
      font-size: 0.12rem;
      color: #595656;
      margin: 0.12rem 0 0.18rem 0.24rem;
    }

    .function-cell-list {
      display: flex;
      flex-wrap: wrap;
      margin: 0 0.13rem;

      .function-cell {
        margin-bottom: 0.26rem;
        text-align: center;
        flex: 0 0 25%;

        .iconfont {
          font-size: 0.3rem;
          color: #eae135;
        }

        .icon-name {
          margin-top: 0.08rem;
          font-size: 0.12rem;
          color: #595656;
        }
      }
    }
  }

  .header-section {
    position: relative;
    z-index: 1;
    height: 1.5rem;
    padding: 0 0.24rem;

    .identity {
      flex: 0 1 0.8rem;
      display: flex;
      align-items: center;

      .identity-left {
        width: 0.5rem;
        height: 0.5rem;
        border-radius: 50%;
      }

      .identity-right {
        flex: 1;
        margin-left: 0.07rem;

        .right-top {
          display: flex;
          align-items: center;
          justify-content: space-between;

          .right-item {
            font-size: 0.12rem;
            color: #a2733d;

            &.offset {
              align-self: flex-end;
            }
          }

          .job {
            display: inline-block;
            color: #595656;
            font-size: 0.12rem;
            background-color: #f8ef94;
            width: 0.62rem;
            text-align: center;
            border-radius: 0.2rem;
            letter-spacing: 0.01rem;
            padding-top: 0.05rem;
            padding-bottom: 0.03rem;
          }
        }

        .right-bottom {
          position: relative;
          top: 0.03rem;
          padding-top: 0.06rem;
          opacity: 0.7;
          font-size: 0.12rem;
          color: #a2733d;
        }
      }
    }

    .data {
      display: flex;
      justify-content: space-between;

      .data-item {
        text-align: center;

        .num {
          margin-top: 0.03rem;
          font-size: 0.14rem;
          color: #00a0e9;
        }

        .data-name {
          font-size: 0.12rem;
          color: #595656;
          letter-spacing: 0.01rem;
          margin-top: 0.12rem;
        }
      }
    }
  }

  .service-section {
    margin-top: 0.1rem;
    min-height: 1.76rem;
  }

  .help-section {
    margin-top: 0.1rem;
  }

  .login-out-btn {
    background-color: #f8ef94;
    color: #595656;
    margin-top: 0.1rem;
    margin-left: 0.25rem;
    width: calc(100% - 0.5rem);
    font-size: 0.12rem;
    letter-spacing: 0.01rem;
    height: 0.32rem;
    box-shadow: none;
  }
}
</style>



