<template>
    <mt-popup class="choose-therapist" popup-transition="popup-fade" :modal=true v-model="isShow">
      <div class="choose-therapist-wapper" @touchmove.prevent>
        <p class="title">请选择提成美体师</p>
        <p class="tip"><span class="iconfont icon-tishi1"></span>不选择默认为商家</p>
        <div class="choose-area">
            <div ref="betterscroll" class="better-scroll">
                <div class="therapist-list">
                    <div v-if="isLoadDb&&therapistList.length==0" class="empty-container">
                        <p class="empty-msg">您还未添加美体师</p>
                        <mt-button size="small" class="add-btn" type="default" @click.native="addHandle">去添加</mt-button>
                    </div>
                    <div v-else v-for="(item,index) in therapistList" class="therapist-cell" :key="item.beautiesid" @click="selectHandle(index)">
                        <div class="left">
                            <my-image class="my-img" :imgUrl="item.headimgurl"></my-image>
                            <div class="name">{{item.username}}</div>
                        </div>
                        <div class="right">
                            <div v-show="index==curIndex" class="circle"></div>
                        </div>
                    </div>
                </div>
            </div>
            <p class="had-choose">提成对象：<span class="name">{{chooseName}}</span></p>
        </div>
        <mt-button size="large" class="commit-btn" type="default" @click.native="commitHandle">确定并扫码</mt-button>
      </div>
    </mt-popup>
</template>
<script>
import myImage from "../../lazyloadImg/lazyImage.vue";
import { mapState } from "vuex";
import BScroll from "better-scroll";
import Vue from "vue";
import { Button, Popup } from "mint-ui";
import "mint-ui/lib/button/style.css";
Vue.component(Button.name, Button);
Vue.component(Popup.name, Popup);

export default {
  name: "choose-therapist",
  components: {
    myImage
  },
  data() {
    return {
      isLoadDb: false, //是否有美体师数据了
      isShow: false,
      chooseName: "商家",
      curPage: 0,
      curIndex: -1 //-1代表未选择
    };
  },
  created() {
    if (this.therapistList.length > 0) {
      this.isLoadDb = true;
      this.initBScroll();
      return;
    }
    this.$http
      .post(this.path + "php/Route.php?c=Store", {
        type: "showBeautiesList",
        bid: this.userid,
        page: this.curPage
      })
      .then(response => {
        this.isLoadDb = true;
        let resultData = response.body.arr;
        this.$store.commit("UPDATA_THERAPIST_LIST", resultData);
        this.initBScroll();
      })
      .catch(response => {
        //请求出错处理
        console.log(response);
      });
  },
  computed: {
    ...mapState(["path", "userid", "therapistList"])
  },
  methods: {
    initBScroll() {
      if (this.betterscroll) return;
      this.$nextTick(() => {
        this.betterscroll = new BScroll(this.$refs.betterscroll, {
          click: true
        });
      });
    },
    commitHandle: function() {
      console.log("点击了确定并扫码按钮");
      let therapistData = null;
      if (this.curIndex != -1) {
        therapistData = this.therapistList[this.curIndex];
      }
      this.$emit("storeCallScan", therapistData);
    },
    selectHandle(chooseIndex) {
      if (this.curIndex == chooseIndex) {
        this.curIndex = -1;
        this.chooseName = "商家";
      } else {
        this.curIndex = chooseIndex;
        let curData = this.therapistList[chooseIndex];
        this.chooseName = curData.username;
      }
    },
    resetHandle() {
      this.chooseName = "商家";
      this.curIndex = -1;
      this.$nextTick(() => {
        if (this.betterscroll) this.betterscroll.refresh();
      });
    },
    show() {
      this.isShow = true;
    },
    hide() {
      this.isShow = false;
    },
    addHandle(event) {
      if (event._constructed) return;   // 去掉betterscroller自带click事件的点击
      this.hide();
      this.$router.push({ name: "therapistList" });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.choose-therapist {
    border-radius: 0.04rem;
    width: 2.8rem;

    .choose-therapist-wapper {
        padding: 0.24rem 0 0.24rem 0;

        .title {
            text-align: center;
            font-size: 0.15rem;
            color: #a2733d;
            letter-spacing: 0.01rem;
        }

        .tip {
            font-size: 0.12rem;
            color: #797979;
            margin: 0.18rem 0 0.08rem 0.16rem;

            .icon-tishi1 {
                font-size: 0.16rem;
                margin-right: 0.02rem;
                position: relative;
                top: -0.01rem;
            }
        }

        .choose-area {
            box-sizing: border-box;
            margin: 0 0.16rem 0 0.16rem;
            border: 1px #ddd solid;
            border-radius: 0.04rem;
            display: flex;
            height: 45vh;
            flex-direction: column;

            .had-choose {
                background-color: #f5f3f3;
                text-align: right;
                color: #666;
                font-size: 0.12rem;
                height: 0.28rem;
                line-height: 0.28rem;
                padding-top: 0.02rem;
                padding-right: 0.12rem;
                border-radius: 0 0 0.04rem 0.04rem;

                .name {
                    color: #333;
                    font-size: 0.13rem;
                }
            }

            .better-scroll {
                flex: 1;
                overflow: hidden;

                .therapist-list {
                    box-sizing: border-box;
                    padding: 0 0.1rem;

                    .empty-container {
                        position absolute;
                        left: 50%;
                        top: 20vh;
                        transform: translate(-50%, -50%);
                        text-align: center;

                        .empty-msg {
                            font-size: 0.13rem;
                            color: #797979;
                        }

                        .add-btn {
                            background-color: #fff;
                            border 1px #BE9D77 solid;
                            color: #BE9D77;
                            margin-top: 0.1rem;
                            font-size: 0.13rem;
                            box-shadow: none;
                            letter-spacing: 0.01rem;
                            height .27rem;
                            width .8rem;
                        }
                    }

                    .therapist-cell {
                        width: 100%;
                        height: 0.6rem;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        border-bottom: 1px #eee dashed;

                        .left {
                            display: flex;
                            align-items: center;

                            .my-img {
                                width: 0.4rem;
                                height: 0.4rem;
                                margin-right: 0.08rem;
                            }

                            .name {
                                font-size: 0.12rem;
                                color: #666;
                            }
                        }

                        .right {
                            margin-right: 0.06rem;
                            width: 0.18rem;
                            height: 0.18rem;
                            border-radius: 50%;
                            border: 1px #a2733d solid;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            .circle {
                                width: 0.1rem;
                                height: 0.1rem;
                                border-radius: 50%;
                                background-color: #a2733d;
                            }
                        }
                    }
                }
            }
        }

        .commit-btn {
            background-color: #f8ef94;
            color: #a2733d;
            margin-top: 0.2rem;
            margin-left: 0.2rem;
            width: calc(100% - 0.4rem);
            font-size: 0.13rem;
            letter-spacing: 0.01rem;
            height: 0.3rem;
            box-shadow: none;
        }
    }
}
</style>





